<template>
  <div id="App">
    <div v-if="isLoading" class="mask">
      <img class="mx-auto" src="data:image/jpeg;base64,">
    </div>
    <div class="base mx-auto container">
      <div class="base__head">
        <h1 class="base__tit">{{tit}}</h1>
      </div>
      <div class="base__body row">
        <Table
        :parent-data="sortData[current]"
        :parent-size="size"
        :parent-current="current"/>
        <div class="base__aside aside">
          <Aside :parent-length="sortData.length" @refresh="setContent"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Table from './components/Table/Table.vue';
import Aside from './components/Aside/Aside.vue';

export default {
  name: 'App',
  components: {
    Table,
    Aside,
  },
  data() {
    return {
      tit: '農村地方美食小吃特色料理',
      isLoading: true,
      data: [],
      current: 0, //現在頁籤
      size: 10, //當頁顯示的資料筆數
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      const api = 'https://data.coa.gov.tw/Service/OpenData/ODwsv/ODwsvTravelFood.aspx';
      this.$http.get(api).then((res) => {
        //關閉loading畫面
        this.isLoading = false;
        this.data = res.data;
      });
    },
    //更新目前頁籤
    setContent(i) {
      this.current = i;
    },
  },
  computed: {
    //資料分組
    sortData() {
      let arr = [];
      this.data.forEach((item, i) => {
        if (i % this.size === 0) {
          arr.push([]);
        }
        arr[parseInt(i / this.size, 10)].push(item);
      });
      return arr;
    },
  },
};
</script>

