<template>
  <div class="aside__list">
    <input type="button"
    v-for="(item,index) in parentLength" :key="index"
    class="aside__btn mx-auto"
    :class="{ '': index != current, 'aside__btn--focus': index === current }"
    :value="index+1"
    @click="atClick">
  </div>
</template>

<script>
export default {
  name: 'Aside',
  props: {
    parentLength: Number,
  },
  data() {
    return {
      current: 0,
    };
  },
  methods: {
    //輸出點選的頁籤值
    atClick(e) {
      this.current = e.target.value - 1;
      this.$emit('refresh', e.target.value - 1);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.aside__list{
  margin: 0 10px;
}

.aside__btn{
  font-size: 15px;
  color: #8F918F;
  margin-bottom: 10px;
  display: block;
  padding-right: 10px;
}
.aside__btn--focus{
  color: #000;
  font-weight: 700;
  border-right: solid 2px #006FB1;
}
</style>
