<template>
  <tr class="table__item item"
  :class="{ '': parentIndex % 2 === 0, 'table__item--dark': parentIndex % 2 !== 0 }">
    <th class="item__index">
      {{ parentCurrent * parentSize + parentIndex + 1}}
    </th>
    <td class="item__city">
      <p>{{ parentItem.City }}</p>
    </td>
    <td class="item__media" @mouseenter="isShow = true" @mouseleave="isShow = false">
      <img class="img-resp" :src="parentItem.PicURL" :alt="parentItem.Name">
      <div v-if="isShow" class="media">
        <img class="img-resp" :src="parentItem.PicURL" :alt="parentItem.Name">
      </div>
    </td>
    <td class="item__name">
      <p v-if="parentItem.Url.length === 0">{{ parentItem.Name }}</p>
      <a v-else :href="parentItem.Url" target="_blank">{{ parentItem.Name }}</a>
    </td>
    <td class="item__intro">
      <p>{{ parentItem.HostWords.substring(0, 49)+'...' }}</p>
    </td>
  </tr>
</template>

<script>
export default {
  name: 'TableRow',
  props: {
    parentItem: Object,
    parentSize: Number,
    parentCurrent: Number,
    parentIndex: Number,
  },
  data() {
    return {
      isShow: false,
    };
  },
};
</script>

<style>
.item__index{
  color: #ACABAA;
  font-weight: 200;
}
tr{
  position: relative;
  font-weight: 600;
}
tr:hover{
  background-color: #EFF4F7;
}
a{
  color: #3F97C6;
}
.table__item--dark{
  background-color: #F9F9F9;
}
.item__media{
  padding: 10px;
}
.item__name{
  text-align: left;
  padding-left: 10px;
}
.item__intro{
  text-align: left;
  padding: 0 5px;
}
.media{
  position: absolute;
  width: 38%;
  height: auto;
  left: 30.5%;
  top: 10%;
  z-index: 3;
  padding: 10px;
  background: #fff;
  box-shadow: 2px 2px 10px rgba(92, 93, 100, 0.5);
}
</style>
